* {
	margin: 0;
	padding: 0;
}

ul, li {
	list-style: none;
}

input {
	border: none;
}

img {
	max-width: 100%;
}

#app {
	margin: 0 auto;
	max-width: 900px; 
}

.header {
	width: 100%;
	height: 70px;
	line-height: 60px;
}

.header_left {
	float: left;
	margin: 10px 0 0 10px;
}

.header_right {
	float: right;
	margin: 30px 20px 0 0 ;
}

.header_right_portrait {
	width: 50px;
	height: 50px;	
	position: relative;
}

.content {
	margin-top: 40px;
}

.content_title {
	text-align: center;
}

.content_list {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack:justify;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.content_list li {
	width: 100px;
	height: 30px;
	line-height: 30px;
	font-size: 16px;
	font-weight: bold;
	text-align: center;
	color: #fff;
	background-color: green;
	border-radius: 12px;
}

.setQuit {
	position: absolute;
	top: 50px;
	left: 0;
	line-height: 24px;
}

.register {
	margin: 100px auto;
	max-width:400px; 
}

.register_title {
	text-align: center;
}

.register_input {
	margin: 0 auto;
	width: 200px;
}

.register_input input {
	margin: 5px 0;
	width: 100%;
	height: 24px;
	line-height: 24px;
	background-color: #e4f30c;
	color: #f30c6c;
}

.register_input div {
	height: 28px;
	line-height: 28px;
	font-weight: bold;
	text-align: center;
}

.is_active {
	color: red;
}

.project {
	width: 100%;
}

.project_header {
	/*display: -webkit-box;
	display: flex;
	-webkit-box-pack:justify;
	justify-content: space-between;*/
	width: 100%;
	height: 50px;
	background-color: #000;
}

.project_load {
	float: left;
	margin-left: 30px;
	line-height: 50px;
	color: #fff;
}

.project_list {
	float: left;
	width: 50%;
	color: #fff;
	line-height: 50px;
	font-size: 24px;
}

.project_list li {
	float: left;
	margin-left: 30px;
	min-width: 100px;
	text-align: center;
}

.project_personal {
	float: right;
	margin-right: 40px;
	line-height: 50px;
}

.project_personal_img {
	height: 50px;
}

.project_personal_img img {
	float: left;
	margin-top: 5px;
	margin-right: 20px;
	width: 40px;
}

.project_personal_img span {
	color: #fff;
}

.project_personal_list {
	display: none;
	font-size: 20px;
	text-align: center;
}

.project_personal_list li {
	height: 50px;
}